import React, { useEffect, useMemo, useState } from "react";
import { collection, getDocs, orderBy, query } from "firebase/firestore";
import { db } from "../firebase/config";
import useGameStore from "../store/gameStore";
import QuestionCard from "../components/QuestionCard";
import GameHeader from "../components/GameHeader";
import EndScene from "../components/EndScene";  // â¬… ì¶”ê°€
import toast from "react-hot-toast";
import "./SurveyGame.css";

const getOptionsArray = (options) => {
  if (!options) return [];
  if (Array.isArray(options)) return options;

  return options
    .split(/[\|,]/)
    .map((o) => o.trim())
    .filter((o) => o.length > 0);
};

const SurveyGame = ({ onComplete }) => {
  const { user, answerQuestion } = useGameStore();

  const [questions, setQuestions] = useState([]);
  const [loading, setLoading] = useState(true);

  const [currentCategory, setCurrentCategory] = useState("");
  const [currentIndex, setCurrentIndex] = useState(0);
  const [answers, setAnswers] = useState({});
  const [startTime, setStartTime] = useState(Date.now());
  const [isFinished, setIsFinished] = useState(false); // â¬… ë í˜ì´ì§€ ì œì–´

  const lang = user?.language || "en";

  useEffect(() => {
    const loadQuestions = async () => {
      try {
        setLoading(true);

        const q = query(collection(db, "questions"), orderBy("id"));
        const snap = await getDocs(q);

        const list = snap.docs.map((doc) => ({
          docId: doc.id,
          ...doc.data(),
        }));

        setQuestions(list);

        if (list.length > 0) {
          setCurrentCategory(list[0].category);
        }
      } catch (err) {
        toast.error("Failed to load questions");
      } finally {
        setLoading(false);
        setStartTime(Date.now());
      }
    };

    loadQuestions();
  }, []);

  const categories = useMemo(
    () => [...new Set(questions.map((q) => q.category))],
    [questions]
  );

  const filteredQuestions = useMemo(
    () => questions.filter((q) => q.category === currentCategory),
    [questions, currentCategory]
  );

  const currentQuestion =
    filteredQuestions.length > 0 ? filteredQuestions[currentIndex] : null;

  const overallProgress = useMemo(() => {
    const answeredCount = Object.keys(answers).length;
    return { current: answeredCount, total: questions.length };
  }, [answers, questions.length]);

  const getCurrentOverallNumber = () => {
    if (!currentQuestion) return 0;
    return questions.findIndex((q) => q.docId === currentQuestion.docId) + 1;
  };

  // ---------------------
  // ğŸ”™ Back ë²„íŠ¼ ê¸°ëŠ¥
  // ---------------------
  const handleBack = () => {
    if (currentIndex > 0) {
      setCurrentIndex(prev => prev - 1);
      return;
    }

    // í˜„ì¬ ì¹´í…Œê³ ë¦¬ì˜ ì²« ë¬¸í•­ì¸ë° ì´ì „ ì¹´í…Œê³ ë¦¬ê°€ ì¡´ì¬í•˜ëŠ” ê²½ìš°
    const currentCatIndex = categories.indexOf(currentCategory);
    if (currentCatIndex > 0) {
      const prevCategory = categories[currentCatIndex - 1];
      const prevCategoryQuestions = questions.filter(
        (q) => q.category === prevCategory
      );
      setCurrentCategory(prevCategory);
      setCurrentIndex(prevCategoryQuestions.length - 1);
    }
  };

  // ---------------------
  // â­ ë‹¤ìŒ ë¬¸í•­ ë˜ëŠ” ì¢…ë£Œ ì²˜ë¦¬
  // ---------------------
  const handleAnswer = (value) => {
    if (!currentQuestion) return;

    const now = Date.now();
    const timeSpent = (now - startTime) / 1000;

    const qId = currentQuestion.docId;

    setAnswers((prev) => ({
      ...prev,
      [qId]: { value, timeSpent },
    }));

    answerQuestion(timeSpent, "good");

    const nextIndex = currentIndex + 1;

    if (nextIndex < filteredQuestions.length) {
      setCurrentIndex(nextIndex);
      setStartTime(Date.now());
      return;
    }

    const answeredIds = new Set(Object.keys({ ...answers, [qId]: true }));

    const remainingCats = categories.filter((cat) =>
      questions.some(
        (q) => q.category === cat && !answeredIds.has(q.docId)
      )
    );

    if (remainingCats.length > 0) {
      setCurrentCategory(remainingCats[0]);
      setCurrentIndex(0);
      setStartTime(Date.now());
      return;
    }

    // ğŸ‰ ì „ì²´ ì„¤ë¬¸ ì™„ë£Œ
    setIsFinished(true);
  };

  if (isFinished) return <EndScene onComplete={onComplete} />;

  if (loading) return <div className="loading-screen">Loading...</div>;

  if (!currentQuestion)
    return <div className="loading-screen">No questions available</div>;

  const formatted = {
    text: currentQuestion.text?.[lang] || currentQuestion.text?.en,
    section: currentQuestion.category,
    type: currentQuestion.type,
    options: getOptionsArray(currentQuestion.options),
    required: true,
  };

  return (
    <div className="survey-container">
      <GameHeader overallProgress={overallProgress} />

      <QuestionCard
        key={currentQuestion.docId}
        question={formatted}
        questionNumber={currentIndex + 1}
        totalQuestions={filteredQuestions.length}
        overallNumber={getCurrentOverallNumber()}
        overallTotal={questions.length}
        onBack={handleBack}       // â¬… ì¶”ê°€
        onAnswer={handleAnswer}
      />
    </div>
  );
};

export default SurveyGame;
